import { AuthLayout, DefaultLayout, ChatLayout } from "@/components/layouts"

export const publicRoute = [
  { path: "*", component: () => import(/* webpackChunkName: "errors-404" */ "@/views/error/NotFound.vue") },
  {
    path: "/auth",
    component: AuthLayout,
    meta: { title: "Login" },
    redirect: "/auth/login",
    hidden: true,
    children: [
      {
        path: "login",
        name: "login",
        meta: { title: "Login" },
        component: () => import(/* webpackChunkName: "login" */ "@/views/auth/Login.vue")
      }
    ]
  },
  {
    path: "/404",
    name: "404",
    meta: { title: "Not Found" },
    component: () => import(/* webpackChunkName: "errors-404" */ "@/views/error/NotFound.vue")
  },
  {
    path: "/500",
    name: "500",
    meta: { title: "Server Error" },
    component: () => import(/* webpackChunkName: "errors-500" */ "@/views/error/Error.vue")
  }
]

export const protectedRoute = [
  {
    path: "/",
    component: DefaultLayout,
    meta: { title: "Home", group: "apps", icon: "" },
    redirect: "/dashboard",
    children: [
      {
        path: "/dashboard",
        name: "Dashboard",
        meta: { title: "Dashboard", group: "apps", icon: "dashboard" },
        component: () => import(/* webpackChunkName: "dashboard" */ "@/views/Dashboard.vue")
      },
      {
        path: "/entries",
        name: "Entries",
        meta: { title: "Entries", group: "apps", icon: "entries" },
        component: () => import(/* webpackChunkName: "dashboard" */ "@/views/Entries.vue")
      },
      {
        path: "/users",
        name: "Users",
        meta: { title: "Users", group: "apps", icon: "users" },
        component: () => import(/* webpackChunkName: "dashboard" */ "@/views/Users.vue")
      },
      {
        path: "/calendar",
        name: "Calendar",
        meta: { title: "Calender", group: "apps", icon: "calendar" },
        component: () => import(/* webpackChunkName: "dashboard" */ "@/views/Calendar.vue")
      },
      {
        path: "/settings",
        name: "Settings",
        meta: { title: "Settings", group: "apps", icon: "settings" },
        component: () => import(/* webpackChunkName: "dashboard" */ "@/views/Dashboard.vue")
      },
      {
        path: "/403",
        name: "Forbidden",
        meta: { title: "Access Denied", hiddenInMenu: true },
        component: () => import(/* webpackChunkName: "error-403" */ "@/views/error/Deny.vue")
      },
      {
        path: "/userdashboard",
        name: "UserDashboard",
        meta: { title: "Dashboard", group: "apps", icon: "dashboard" },
        component: () => import(/* webpackChunkName: "dashboard" */ "@/views/UserDashboard.vue")
      },
    ]
  },

]
